/*
    Hello.css는 Hello컴포넌트에 대한 스타일이지만 렌더링시
    <head>태그 안에 포함된다
    즉 모든 컴포넌트에 스타일이 다 적용된다 
    ※각 컴포넌트에 독립적인 스타일을 작성하려면
    컴포넌트명.module.css 작성후 해당 컴포넌트에서 아래처럼 import한다
    import  styles from "./Hello.module.css";

    요소에서는 className={styles.box}
    브라우저 개발자도수의 검사에서 보면 아래처럼 class가 생성됨
    <div class="Hello_box__tT7h9">컴포넌트명.module.css</div>
    즉 컴포넌트명_클래스명_해쉬값
*/

h1{
    color:#FFFFFF;
    background-color: yellow;
    opacity: 1;
}
.box{
    width:450px;
    height: 50px;
    background-color: rgb(112, 112, 228);
}